.qr-container {
    max-width: 1300px;
    margin: 0 auto;
    position: relative;
}

section {
    position: relative;
}

table {
    width: 100%;
    border: 1px solid #dedede;

    thead {
        th {
            background-color: #ededed;
            padding: 10px;
            text-align: center;
            font-weight: 500;
            text-transform: uppercase;
            font-size: 15px;
            border-right: 1px solid #dedede;
            border-bottom: 1px solid #dedede;
            position: relative;
            white-space: nowrap;

            &:last-child {
                border-right: 0;
            }
        }
    }

    tbody {
        td {
            background-color: #fff;
            padding: 12px;
            font-size: 15px;
            font-weight: 400;
        }
    }
}

.main-body {
    min-height: calc(100vh - 70px)
}

.stamp-create {
    text-align: center;
    background-image: url('/images/wizard-profile.jpg');
    position: relative;
    min-height: calc(100vh - 70px);
    background-position: center center;
    background-size: cover;

    &:before {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #000000;
        opacity: .3;
        z-index: 1;
    }

    .create-container {
        max-width: 950px;
        margin: 0 auto;
        padding: 30px;
        background-color: #fff;
        position: relative;
        z-index: 3;
    }

    .step-wrapper {
        position: relative;

        .active-menu {
            position: absolute;
            width: 25%;
            padding: 12px;
            text-transform: uppercase;
            -webkit-font-smoothing: subpixel-antialiased;
            background-color: #4caf50;
            top: -4px;
            left: 0;
            border-radius: 4px;
            color: #FFFFFF;
            cursor: pointer;
            font-weight: 500;
            box-shadow: 0 16px 26px -10px rgba(76, 175, 80, 0.56), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(76, 175, 80, 0.2);
            transition: all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1) 0s;
            pointer-events: none;

            &.step-1 {
                left: -5%;
            }

            &.step-2 {
                left: 25%;
            }

            &.step-3 {
                left: 50%;
            }

            &.step-4 {
                left: 80%;
            }
        }
    }

    .s-step {
        padding: 0;
        margin: 0;
        list-style: none;
        width: 100%;
        background-color: rgba(200, 200, 200, .2);
        @include clearfix();

        li {
            width: 25%;
            float: left;
            line-height: 42px;

            a {
                font-size: 16px;
                text-transform: uppercase;
                font-weight: 500;
                color: #555;
            }
        }
    }

    .step-conten {
        max-width: 500px;
        margin: 0 auto;
        margin-top: 30px;
    }
}

.add-user {
    max-width: 500px;
    margin: 0 auto;
}

.user-page {}


.demo-editor {
    border: 1px solid #d9d9d9;
    padding: 0px 15px;
}

.top-section {
    padding: 80px 0;
}

input[disabled],
select[disabled] {
    background-color: #e8e8e8;
    color: #5f5f5f;
}

.group-btn {
    @include clearfix();

    .search-group {
        float: left;
        margin-top: 15px;

        input {
            width: 300px;
            height: 36px;
            border: 1px solid #e1e3ea;
            border-radius: 4px;
            outline: 0;
            padding: 0 10px;
            transition: all .2s ease-in;

            &:focus {
                border: 1px solid #594cff;
            }
        }
    }

    .date-group {
        float: left;
        margin-left: 15px;
        margin-top: 15px;

        input {
            width: 220px;
            height: 36px;
            border: 1px solid #e1e3ea;
            border-radius: 4px;
            outline: 0;
            padding: 0 10px;
            transition: all .2s ease-in;

            &:focus {
                border: 1px solid #594cff !important;
            }
        }

        >div {
            &:first-child {
                input {
                    border-top-right-radius: 0;
                    border-bottom-right-radius: 0;
                    border-right: 0;
                }
            }

            &:last-child {
                input {
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                }
            }
        }
    }

    .filter-tab {
        float: left;
        margin-left: 15px;
        margin-top: 15px;

        select {
            height: 36px;
            min-height: 36px;
            outline: none;
            padding: 0 15px;
            min-width: 150px;
        }
    }
}

.sortIcon {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    text-align: right;
    @include clearfix();
    line-height: 2px;
    padding-top: 11px;
    padding-right: 7px;

    span {
        display: inline-block;
        width: 12px;
        height: 7px;
        background-image: url('/images/sort.png');
        background-size: 100%;
        background-repeat: no-repeat;

        &.sUp {
            background-position: 0 -17px;
            margin-bottom: 2px;
        }

        &.sDowwn {
            background-position: 0 -27px;
        }
    }

    &.DES {
        span {
            &.sDowwn {
                background-position: 0 -9px;
            }
        }
    }

    &.ASC {
        span {
            &.sUp {
                background-position: 0 0;
            }
        }
    }
}

.wrap-pop-info {
    .close-pop {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 101;
    }

    .pop-info {
        position: absolute;
        top: 0;
        right: 0;
        background-color: #fff;
        height: 100vh;
        overflow: auto;
        z-index: 109;
        width: 600px;
        box-shadow: 1px 1px 11px 0px rgba(0, 0, 0, 0.55);
        padding: 30px 0;

        .main-name {
            color: #6d6d6d;
            font-size: 26px;
            font-weight: 500;
            text-transform: uppercase;
        }

        .top-info {
            margin-bottom: 20px;
            padding: 0 30px;

            span {
                display: inline-block;
                margin-right: 30px;
                color: #a5a5a5;
                font-size: 16px;
                font-weight: 400;
            }
        }

        .line-breack {
            background-color: #f7f7f7;
            font-size: 16px;
            text-transform: uppercase;
            color: #8c8c8c;
            margin: 0;
            line-height: 40px;
            padding: 0 30px;
        }

        .group-text {
            padding: 0 30px;
            margin-bottom: 0;
            border-bottom: 1px solid #e2e2e2;
            line-height: 40px;

            label {
                color: #888;
            }

            input[disabled],
            select[disabled] {
                border: 0;
                background-color: transparent;
                text-align: right;
            }
        }
    }
}

.show-pass {
    position: absolute;
    right: 10px;
    top: 40px;
    color: #594cff;
}

.select-lo {
    width: 320px;
    float: left;
    transform: translateY(15px);
}

.report-page {
    padding: 30px 0;
}

.has-label {
    label {
        text-align: left;
        display: block;
    }
}

.top-report {
    margin: 15px 0 30px 0;

    ul {
        padding: 0;
        margin: 0;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;

        li {
            display: -ms-flexbox;
            display: -webkit-box;
            display: flex;
            -ms-flex-align: center;
            -ms-flex-align: center;
            -webkit-box-align: center;
            align-items: center;
            padding: .65rem .5rem;
            position: relative;
            transition: all .3s;
            -webkit-transition: all .3s;
            width: 20%;

            a {
                background-color: #fff;
                display: block;
                box-shadow: 0px 1px 2px 0px rgba(44, 45, 58, 0.14);
                width: 100%;
                text-align: left;
                padding: 10px;
            }

            .title {
                font-weight: 500;
                text-transform: uppercase;
                font-size: 16px;
                font-weight: 500;
                color: #858796;
            }
        }
    }
}

.react-dropdown-select {
    z-index: 101 !important;
    background-color: #fff;
}

.group-btn {
    position: relative;
    z-index: 100;
}

.table-panel {
    position: relative;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    border: 1px solid #e1eaea;
    width: 100%;
    background-color: #fff;
    text-align: left;
    padding: 10px;
    margin-bottom: 30px;

    .title {
        color: rgba(24, 35, 89, 0.85);
        font-size: 16px;
        font-weight: 500;
        margin-bottom: 15px;
    }

    .top-ico {
        position: absolute;
        top: 10px;
        right: 10px;
    }
}

.char-title {
    font-weight: 400;
    font-size: 15px;
    text-align: center;
    color: #545454;
    margin-bottom: 20px;
}

.location-report {
    padding-bottom: 30px;

    .top-location {
        border-bottom: 1px solid rgba(16, 114, 206, 0.25);
        padding: 8px 0;
        margin-bottom: 15px;
        font-weight: 500;
        color: rgba(24, 35, 89, 0.85);
        font-size: 16px;
    }

    .location-name {
        color: rgba(24, 35, 89, 0.85);
        font-size: 14px;
        float: left;
    }

    .view {
        float: right;
    }

    .list-progress-bar {
        .progress-location {
            margin-bottom: 25px;

            .progress-outner {
                height: 9px;
                border-radius: 100px;
                position: relative;
                background-color: #eaeaea;
                box-shadow: 0 .125rem .25rem rgba(0, 0, 0, .075);
                overflow: hidden;
                margin-top: 5px;
                .progress-inner {
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background-image: linear-gradient(45deg, rgba(255, 255, 255, .15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, .15) 50%, rgba(255, 255, 255, .15) 75%, transparent 75%, transparent);
                    background-size: 1rem 1rem;
                    background-color: #0084ff;
                    color: #e5f2ff;
                    animation: progress-bar-stripes 1s linear infinite;
                }
            }

            &:nth-child(2) {
                background-color: #fff;

                .progress-outner {
                    .progress-inner {
                        background-color: #fa625e;
                        color: #fee5e4;
                    }
                }
            }

            &:nth-child(3) {
                .progress-inner {
                    background-color: #94df4a;
                    color: #e5fcd0;
                }
            }
        }
    }
}

@keyframes progress-bar-stripes {
    0% {
        background-position: 1rem 0;
    }

    100% {
        background-position: 0 0;
    }
}


